<template>
  <div id="qeditor">
    <quill-editor
      :key="custom"
      ref="qeditor"
      v-model="content"
      :options="editorOption"
      @change="onEditorChange($event)"
    />
  </div>
</template>
<script>
import $ from "jquery";
import registerCustomBlot from "../Quill/blot";
import "./index.scss";
import Quill from "quill"; // 引入编辑器
import { quillEditor } from "vue-quill-editor";
import html_lens from './html_lens';
import html_stick from './html_stick';

const markIcon = require("../../assets/center.svg");
// 调整图片大小
import ImageResize from "quill-image-resize-module";
Quill.register("modules/imageResize", ImageResize);
// 自定义字体大小
const Size = Quill.import("attributors/style/size");
const Delta = Quill.import("delta");
Size.whitelist = [
  "12px",
  "14px",
  "16px",
  "18px",
  "20px",
  "22px",
  "24px",
  "36px",
  "48px",
];
Quill.register(Size, true);
registerCustomBlot(Quill);

// 自定义字体样式
const Font = Quill.import("attributors/style/font");
const fonts = ["Open Sans", "Anton", "Playfair", "Asul"];
Font.whitelist = fonts; // 将字体加入到白名单
Quill.register(Font, true);


export default {
  name: "qeditor",
  props: {
    custom: String,
  },
  components: {
    quillEditor,
  },
  created() {
    this.editorOption.modules.toolbar.container[14] = [
      { RN1: ["RL", "RC", "RR"] },
    ];
  },
  mounted() {
    this.setHtml();
  },

  data() {
    return {
      content: ``,
      editorOption: {
        modules: {
          toolbar: {
            container: [
              ["bold", "italic", "underline", "strike"], // 加粗 斜体 下划线 删除线 -----['bold', 'italic', 'underline', 'strike']
              ["blockquote", "code-block"], // 引用  代码块-----['blockquote', 'code-block']
              [{ list: "ordered" }, { list: "bullet" }], // 有序、无序列表-----[{ list: 'ordered' }, { list: 'bullet' }]
              [{ header: 1 }, { header: 2 }],
              [{ script: "sub" }, { script: "super" }], // 上标/下标-----[{ script: 'sub' }, { script: 'super' }]
              [{ indent: "-1" }, { indent: "+1" }],
              [{ size: Size.whitelist }], // 字体大小
              [{ header: [1, 2, 3, 4, 5, 6, false] }], // 标题-----[{ header: [1, 2, 3, 4, 5, 6, false] }]
              [
                { color: ["#fff", "#969799", "#d1120d", "#000"] },
                { background: ["#fff", "#969799", "#d1120d", "#000"] },
              ], // 字体颜色、字体背景颜色-----[{ color: [] }, { background: [] }]
              ["clean"], // 清除文本格式-----['clean']

              [{ font: fonts }], //显示字体选择
              [{ align: [] }], // 对齐方式-----[{ align: [] }]
              ["image"], // 链接、图片、视频-----['link', 'image', 'video']
              //   ['RN2']

              //工具栏设置下拉菜单
            ],
            handlers: {
              that: this,
              update: this.emitHtml,
              RN1: function (v) {
                $("#rn").remove();
                const pString = `
                  <p>
                    <span style="font-family:Asul;font-size: 22px;" >Room No. </span>
                    <span style="background: #fff; ">___________________</span>
                    &nbsp;
                  </p>
                `

                let value = this.quill.clipboard.convert(pString); //把html转换成delta格式
                let range = this.quill.getSelection(true);
                const delta = new Delta()
                  .retain(range.index)
                  .delete(range.length)
                  .insert("\n")
                  .concat(value)
                  .insert("\n");
                this.quill.updateContents(delta, Quill.sources.USER);
                const { focusOffset, focusNode } = window.getSelection();
                let target = "";
                if (focusOffset) {
                  target = focusNode.parentNode.nextSibling;
                } else {
                  target = focusNode.nextSibling;
                }
                const rnBox = target.lastElementChild;
                rnBox.style.height = "33px";
                rnBox.style.borderRadius = "4px";
                rnBox.id = "rn-box";
                rnBox.setAttribute('contenteditable', false)
                target.id = "rn";
                target.className = "custom-" + v;
                this.content = this.handlers.update();
              },
              RN2: function () {
                $("#rn").remove();

                const pString = `
                  <p class="ql-align-center">
                    <span style="font-family:Asul;font-size: 22px;">Room No. [001]</span>
                  </p>
                `

                let value = this.quill.clipboard.convert(pString); //把html转换成delta格式
                let range = this.quill.getSelection(true);
                const delta = new Delta()
                  .retain(range.index)
                  .delete(range.length)
                  .insert("\n")
                  .concat(value)
                  .insert("\n");
                this.quill.updateContents(delta, Quill.sources.USER);
                const { focusOffset, focusNode } = window.getSelection();
                let target = "";
                if (focusOffset) {
                  target = focusNode.parentNode.nextSibling;
                } else {
                  target = focusNode.nextSibling;
                }
                const spanBox = target.lastElementChild;
                spanBox.style.height = "33px";
                spanBox.className = "rn-box";
                spanBox.set
                target.id = "rn";
                this.content = this.handlers.update();
              },
            },
          },

          //   调整图片大小
          imageResize: {
            displayStyles: {
              backgroundColor: "black",
              border: "none",
              color: "white",
            },
            modules: ["Resize"],
          },
        },
      },
    };
  },

  methods: {
    emitHtml() {
      const html = document
        .querySelector(".ql-editor")
        .innerHTML.replace("[", "")
        .replace("]", "");
      this.$emit("editor", html);
    },
    onEditorChange({ html }) {
      this.$emit("editor", html.replace("[", "").replace("]", ""));
    },
    /* 设置编辑器的默认内容 */
    setHtml(v) {
      let html = "";
      let defHtml = "";
      if (this.custom === "lens") {
        html = localStorage.getItem("html_lens");
        defHtml = html_lens;
      } else {
        html = localStorage.getItem("html_stick");
        defHtml = html_stick;
      }
      this.$refs.qeditor.$el.querySelector(".ql-editor").innerHTML = "";
      this.$refs.qeditor.$el.querySelector(".ql-editor").innerHTML =
        v === 1 ? defHtml : html || defHtml;
    },
  },
  watch: {
    custom: {
      handler(v) {
        this.$nextTick(() => {
          this.setHtml();
          this.editorOption.modules.toolbar.container[14] =
            v === "lens" ? ["RN2"] : [{ RN1: [ "RC", "RL","RR"] }];
          const RN2 = $(".ql-RN2")[0];
          if (RN2) {
            const img = document.createElement("img");
            img.width = 22;
            img.height = 22;
            img.src = markIcon;
            RN2.appendChild(img);
          }
        });
        // 删除整块
        this.$nextTick(() => {
          this.$refs.qeditor.$el.addEventListener('keydown', (e) => {
            // console.log(window.getSelection().focusNode.parentNode)
            if(e.keyCode !== 8) {
              return false
            }
            const maybeRnBox = window.getSelection().focusNode.parentNode
            if( maybeRnBox.getAttribute('contenteditable') === 'false' && (maybeRnBox.id === 'rn-box' || maybeRnBox.className === 'rn-box')) {
              maybeRnBox.parentNode.remove();
            }
          })
        })
      },
      immediate: true,
    },
  },
};
</script>

<style lang="scss">
[class^="custom"] {
  display: flex;
  align-items: center;
  &.custom-RL {
    justify-content: start;
  }
  &.custom-RC {
    justify-content: center;
  }
  &.custom-RR {
    justify-content: end;
  }
}
.ql-RN1 {
  width: 50px;
}
</style>
