      return arr.slice(0, num_mpks);
    },
    mapIcon() {
      const obj = {};
      this.$nextTick(() => {
        this.mpkList.forEach((v) => {
          obj[v.icon] = v.description;
        });
      });
      return obj;
    },
    hasWhiteInEditorHtml() {
      if (this.isGHP61) return false;
      const WHITE_TXT = 'style="color: rgb(255, 255, 255);"';
      return (this.editorHtml || "").includes(WHITE_TXT);
    },
    hasRedInEditorHtml() {
      if (this.isGHP61) return true;
      const RED_TXT = "color: rgb(209, 18, 13);";
      return (this.editorHtml || "").includes(RED_TXT);
    },
  },
  watch: {
    hasWhiteInEditorHtml() {
      this.table.pms = "Pantone Cool Gray 6 C";
    },
  },
  methods: {
    changeColor(v) {
      if (v) {
        this.table.pms = "Pantone Cool Gray 6 C";
      } else {
        this.table.pms = "";
      }
    },
    reset() {
      this.selectMpk = [
        { description: "Front Desk", icon: "FD-1" },
        { description: "Room Service", icon: "RS-1" },
        { description: "Emergency", icon: "EM-1" },
        { description: "Wake-Up", icon: "WU-1" },
        { description: "Restaurant", icon: "RS-2" },
        { description: "Housekeeping", icon: "HK-1" },
      ];
      if(!this.isGHP61){
      this.table = {
        project: "",
        enterprise: "",
        quantity: "",
        contact: "",
        phone: "",
        email: "",
        color: true,
        pms: "Pantone Cool Gray 6 C",
        specification: "",
      };
      this.rn = "";
      this.$refs.editor.setHtml(1);
      }
      this.$message.success(this.$t("RESET_OK"));
    },
    saveHtml() {
      if (this.custom === "lens") {
        localStorage.setItem("html_lens", this.editorHtml);
      } else {
        localStorage.setItem("html_stick", this.editorHtml);
      }
      localStorage.setItem("pages", this.rn);
      this.saveMpkList();
    },
    saveMpkList() {
      localStorage.setItem("selectMpk", JSON.stringify(this.selectMpk));
      localStorage.setItem("table", JSON.stringify(this.table));
      localStorage.setItem("locale", this.locale);
      this.$message.success(this.$t("SAVE_OK"));
    },
    setEditorHtml(h) {
      this.editorHtml = h;
    },
    // 添加icon
    AddIcon(i, none) {
      if (none) return false;
      this.selectedIcon = this.selectMpk[i].icon || "";
      this.editXML = i;
      this.dialogVisible = true;
    },
    // 保存选择的icon
    saveMPK() {
      this.dialogVisible = false;
      this.selectMpk[this.editXML].icon = this.selectedIcon;
      this.selectMpk[this.editXML].description =
        this.mapIcon[this.selectedIcon];
    },
    // 获取房间值和个数
    getRN() {
      const rn = this.rn.replace(/\s/, "");
      if (/[^\d-,，]/.test(rn)) {
        return [];
      }
      const nums = [];
      rn.split(/[,，]/).forEach((n) => {
        if (/^\d+-\d+$/.test(n)) {
          const [start, end] = n.split("-");
          for (let i = +start; i <= +end; i++) {
            nums.push((i + "").padStart(start.length, "0"));
          }
          return false;
        }
        if (/^\d+$/.test(n)) {
          nums.push(n);
          return false;
        }
      });
      return nums;
    },

    // 生成文档页面
    getDoc() {
      if ($(".ghp2 #rn").length && this.custom === "lens") {
        const rnBox = $(".ghp2 #rn-box")[0];
        const pos = $(".ghp2 #rn")[0].classList.value.split("-")[1];
        $("#rn-line").remove();
        const line = document.createElement("span");
        line.id = "rn-line";
        const mark = document.createElement("span");
        mark.id = "rn-mark";
        mark.innerText = "Transparent Area";
        mark.style.cssText = `
            right: -220px;
            top: -15px;
            position: absolute;
            width: 200px;
            font-weight:bold;
            font-size: 22px;
      `;
        line.style.cssText =
          `
            left: 50%;
            top: 50%;
            position: absolute;
      ` +
          `
      ${
        pos == "RL"
          ? "width: 400px;"
          : pos == "RC"
          ? "width: 240px;"
          : "width: 150px;"
      }`;
        rnBox.style.position = "relative";
        rnBox.appendChild(line);
        line.appendChild(mark);
      }
      const dmain = document.querySelector(".d-main");
      dmain.innerHTML = "";
      dmain.innerHTML = this.$refs.print.outerHTML;
      return this.$refs.printDoc.outerHTML;
    },
    // 获取镜片模式的批量房间
    getLens() {
      const RNS = this.getRN().slice(0, 29106); //一百张限制
      // 按每页294个进行分割，
      const pages = RNS.reduce((pages, cur, i) => {
        const pageI = i % 294;
        if (pageI === 0) {
          pages.push(Array(294).fill(""));
        }
        const lastPage = pages[pages.length - 1];
        lastPage[pageI] = cur;
        return pages;
      }, []);

      return `
        <div id='numBox-lens'>
          ${pages
            .map((nums) => {
              return `
                <div class="page">
                  ${nums.map((n) => `<div class="box">${n}</div>`).join("")}
                </div>
              `;
            })
            .join("")}      
        </div>
      `;
    },
    // 获取贴纸模式的批量房间
    getStick() {
      const nums = this.getRN().slice(0, 1980); //一百张限制
      const rnBox = $(".ghp2 .rn-box");
      const target = rnBox[rnBox.length - 1].innerText.replace(/[^0-9]/gi, "");
      const stick = this.$refs.print.outerHTML;
      let pages = [];
      let str = "";

      nums.forEach((v, i) => {
        if (i && (i + 1) % 20 === 0) {
          str += stick.replaceAll(target, v);
          pages.push(`<div class="page">${str}</div>`);
          str = "";
        } else {
          str += stick.replaceAll(target, v);
        }
      });
      //   不凑满一页的情况补全
      let add = 0;
      if (nums.length > 1 && nums.length % 20) {
        add = (parseInt(nums.length / 20) + 1) * 20 - nums.length;
        pages.push(
          `<div class="page add">${
            str + stick.replace(target, "        ").repeat(add)
          }</div>`
        );
      }
      const numBox = document.createElement("div");
      numBox.id = "numBox-stick";
      numBox.innerHTML = pages.join("");
      return numBox.outerHTML;
    },

    validate() {
      const { quantity, contact, phone, email } = this.table;
      const errors = [];
      !quantity && errors.push("quantity");
      !contact && errors.push("contact");
      !phone && errors.push("phone");
      !email && errors.push("email");
      //   表格必填项验证
      if (errors.length) {
        this.$message.error(this.$t("INPUT_REQUIRED"));
        return false;
      }
      //   房间号格式验证
      let n = this.getRN();
      if (this.rn && !n.length) {
        this.$message.error(this.$t("ROOM_NUM_ERR"));
        return false;
      }
      return true;
    },

    // 打印pdf
    print() {
      if (!this.validate()) {
        return false;
      }

      const style = "./iconfonts/iconStyle.css";
      const style2 = "./iconfonts/quill.css";
      const scripts = []; //通过launchPrint延长时间，加载资源

      const section = document.createElement("section");
      let customRN = "";
      if (
        !this.isGHP61 &&
        this.rn &&
        ($(".ghp2 #rn-box").length || $(".ghp2 .rn-box").length)
      ) {
        customRN = this.custom === "lens" ? this.getLens() : this.getStick();
      }
      section.innerHTML = this.getDoc() + customRN;
      const printCallback = ({ launchPrint }) =>
        setTimeout(() => {
          launchPrint();
        }, 500);
      const d = new Printd();
      if (!this.isGHP61 && this.custom === "lens") {
        this.$confirm(this.$t("CONFIRM_LOGO_COLOR"),this.$t("TIP"), {
          showClose: false,
          confirmButtonText: this.$t("CONFIRM"),
          cancelButtonText: this.$t("CLOSE"),
        }).then(() => {
          d.print(
            section,
            [
              style,
              style2,
              printCss,
              docCss,
              `
        @page {
        size: ${
          this.isGHP61 || this.custom === "lens"
            ? "A4 landscape"
            : "A3 portrait"
        };
        margin: 5mm;
        }`,
            ],
            scripts,
            printCallback
          );
        });
      } else {
        d.print(
          section,
          [
            style,
            style2,
            printCss,
            docCss,
            `
        @page {
        size: ${
          this.isGHP61 || this.custom === "lens"
            ? "A4 landscape"
            : "A3 portrait"
        };
        margin: 5mm;
        }`,
          ],
          scripts,
          printCallback
        );
      }
    },
  },
};
</script>
