export default function  registerCustomBlot (q) {

    const BlockEmbed = q.import('blots/block/embed')
    class customBlot extends BlockEmbed {
        static create(value) {
            const node = super.create(value);
            node.setAttribute('contenteditable', true);
            let img=document.createElement('img')
            // let span=document.createElement('p')
            img.src=value.imgSrc
            node.innerText=value.text
            // span.innerText=value.text
            // node.appendChild(span)
            node.appendChild(img)
            return node
        }
        static value(node) {
            return node.innerHTML
          }
    }

    customBlot.blotName = 'customBlot';
    customBlot.className = 'custom-blot';
    customBlot.tagName = 'p';
    
    q.register(customBlot, true);

}